body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}
button {
  cursor: pointer;
}
ul {
  padding-inline-start: 0px;
  margin: 0px;
  padding: 0px;
}
a {
  color: inherit;
  text-decoration: none;
}
li {
  list-style-type: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
